body {
    font-family: Arial, Helvetica, sans-serif;
    background-image: url("https://images.homedepot-static.com/productImages/414d5eeb-4b8c-4874-974f-06c6c1c77eae/svn/york-wallcoverings-wallpaper-ds7812-64_1000.jpg")
}



.navbar {
    position:fixed;
    width:100%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    background-color: darkgray;
    z-index: 1;
    height:70px;
}
.navbar a, span {
    text-align: center;
    width: 33.3%;
    text-decoration: none;
    color: #f1f1f1;
    font-weight: bold;
    font-size: 30px;

}
.jumbotron{
    padding-top: 150px;
    text-align:center;
    background-image: url("https://www.iwantwallpaper.co.uk/images/galerie-wallcoverings-galerie-official-disney-mickey-mouse-logo-pattern-cartoon-childrens-wallpaper-mk3015-5-p3306-7445_image.jpg")
}

h1 {
    font-size: 60px;
}

p {
    font-size: 30px;
}
.card {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1rem;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;

  }
footer {
    background-color:  darkgray;
    text-align:center;
    padding:20px;
    margin-top:30px;
}

footer img {
    height:40px;
    width:40px;
    margin: 0 10px 15px 10px;
}
